<template>
  <div class="login">
    <div class="login-container">
      <p class="login-container-title">登录</p>
      <van-cell-group class="login-container-form">
        <van-field
          v-model="username"
          clearable
          left-icon="contact"
          placeholder="手机号">
            <van-icon name="user-o" size="0.8rem" slot="left-icon"/>
          </van-field>

        <van-field
          v-model="password"
          type="password"
          placeholder="密码">
            <van-icon class="iconfont" class-prefix='icon' size="0.8rem" slot="left-icon" name='mima'></van-icon>
          </van-field>
      </van-cell-group>
      <van-button type="info" class="login-container-btn">登录</van-button>
      <p class="login-container-forget">忘记密码</p>
    </div>
  </div>
</template>

<script>
import { Field, CellGroup, Icon, Button } from 'vant'
export default {
  components: {
    [Icon.name]: Icon,
    [Button.name]: Button,
    [Field.name]: Field,
    [CellGroup.name]: CellGroup
  },
  data () {
    return {
      username: '',
      password: ''
    }
  }
}
</script>

<style scoped>
.login{
  background-color: #0684bf;
  height: 100vh;
  position: relative;
  font-size: 16px;
}
.login-container{
  position: absolute;
  top: 130px;
  left: 50%;
  transform: translateX(-50%);
  background-color: #fff;
  width: 80%;
  height: 70vh;
  text-align: center;
}
.login-container-title{
  font-weight: bold;
  text-align: center;
}
.login-container-form{
  margin: 100px 0;
}
.login-container-btn{
  width: 260px;
  height: 30px;
  line-height: 30px;
}
.login-container-forget{
  font-size: 12px;
  padding-top: 20px;
}
</style>



